import NcHighlight from '@nextcloud/vue/dist/Components/NcHighlight.js';
export default {
  name: 'SearchResult',
  components: {
    NcHighlight
  },
  props: {
    thumbnailUrl: {
      type: String,
      default: null
    },
    title: {
      type: String,
      required: true
    },
    subline: {
      type: String,
      default: null
    },
    resourceUrl: {
      type: String,
      default: null
    },
    icon: {
      type: String,
      default: ''
    },
    rounded: {
      type: Boolean,
      default: false
    },
    query: {
      type: String,
      default: ''
    },
    /**
     * Only used for the first result as a visual feedback
     * so we can keep the search input focused but pressing
     * enter still opens the first result
     */
    focused: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      hasValidThumbnail: this.thumbnailUrl && this.thumbnailUrl.trim() !== '',
      loaded: false
    };
  },
  computed: {
    isIconUrl() {
      // If we're facing an absolute url
      if (this.icon.startsWith('/')) {
        return true;
      }

      // Otherwise, let's check if this is a valid url
      try {
        // eslint-disable-next-line no-new
        new URL(this.icon);
      } catch {
        return false;
      }
      return true;
    }
  },
  watch: {
    // Make sure to reset state on change even when vue recycle the component
    thumbnailUrl() {
      this.hasValidThumbnail = this.thumbnailUrl && this.thumbnailUrl.trim() !== '';
      this.loaded = false;
    }
  },
  methods: {
    reEmitEvent(e) {
      this.$emit(e.type, e);
    },
    /**
     * If the image fails to load, fallback to iconClass
     */
    onError() {
      this.hasValidThumbnail = false;
    },
    onLoad() {
      this.loaded = true;
    }
  }
};